import React from 'react';
import PropTypes from 'prop-types';

function Index(props) {
  const { iname, size } = props;

  return (
    <i className={`${size} material-icons `}>
      {iname}
    </i>
  );
}

Index.propTypes = {
  iname: PropTypes.string,
  size: PropTypes.string,
};
Index.defaultProps = {
  iname: 'add',
  size: 'small',
};

export default Index;
